<template>
  <div id="SysUpdateLog">
    <div class="left_menu">
      <ul>
        <li
          :class="{ active: curIndex === index }"
          v-for="(item, index) in menulist"
          :key="index"
          @click="selectMenu(index, item)"
        >
          {{ item }}
        </li>
      </ul>
    </div>
    <div class="right_con">
      <h2>系统更新日志</h2>
      <div class="topOpt">
        <el-button type="primary" size="mini" @click="searchData('前端')"
          >前端</el-button
        >
        <el-button type="primary" size="mini" @click="searchData('后端')"
          >后端</el-button
        >
        <el-button type="primary" size="mini" @click="addNew">新增</el-button>
        <el-button type="primary" size="mini" @click="editData"
          >修改数据</el-button
        >
        <el-button type="primary" size="mini" @click="deleteData"
          >删除数据</el-button
        >
      </div>
      <div class="TableCon">
        <table class="tablebox">
          <thead>
            <tr>
              <th><div></div></th>
              <th><div>人员</div></th>
              <th><div>时间</div></th>
              <th><div>内容</div></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in tableData" :key="index">
              <td>
                <div>
                  <input type="checkbox" :value="item.id" />
                </div>
              </td>

              <td>
                <div>{{ item.update_person }}</div>
              </td>
              <td>
                <div>{{ item.update_time }}</div>
              </td>
              <td>
                <div>{{ item.update_info }}</div>
              </td>
            </tr>
          </tbody>
        </table>
        <el-dialog
          title="新增日志"
          :visible.sync="addDialogVisible"
          width="400px"
          :before-close="cancelAddForm"
        >
          <div class="addForm">
            <div class="form_item">
              <p>职位：</p>
              <el-select v-model="addForm.update_area">
                <el-option
                  v-for="(item, index) in jobs"
                  :label="item"
                  :value="item"
                  :key="index"
                >
                  {{ item }}
                </el-option>
              </el-select>
            </div>
            <div class="form_item">
              <p>用户名：</p>
              <el-input v-model="addForm.update_person"></el-input>
            </div>
            <div class="form_item">
              <p>时间：</p>
              <el-date-picker
                v-model="addForm.update_time"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择日期"
              >
              </el-date-picker>
            </div>
            <div class="form_item">
              <p>内容：</p>
              <el-input
                v-model="addForm.update_info"
                type="textarea"
                :rows="3"
                placeholder="请输入内容"
              ></el-input>
            </div>
            <div class="form_item">
              <el-button @click="submitAddForm" type="primary" size="mini"
                >提交</el-button
              >
              <el-button @click="cancelAddForm" type="info" size="mini"
                >取消</el-button
              >
            </div>
          </div>
        </el-dialog>
        <el-dialog
          title="编辑日志"
          :visible.sync="editDialogVisible"
          width="400px"
          :before-close="
            () => {
              editDialogVisible = false;
            }
          "
        >
          <div class="addForm">
            <div class="form_item">
              <p>职位：</p>
              <el-select v-model="editForm.update_area">
                <el-option
                  v-for="(item, index) in jobs"
                  :label="item"
                  :value="item"
                  :key="index"
                >
                  {{ item }}
                </el-option>
              </el-select>
            </div>
            <div class="form_item">
              <p>用户名：</p>
              <el-input v-model="editForm.update_person"></el-input>
            </div>
            <div class="form_item">
              <p>时间：</p>
              <el-date-picker
                v-model="editForm.update_time"
                value-format="yyyy-MM-dd"
                type="date"
                placeholder="选择日期"
              >
              </el-date-picker>
            </div>
            <div class="form_item">
              <p>内容：</p>
              <el-input
                v-model="editForm.update_info"
                type="textarea"
                :rows="3"
                placeholder="请输入内容"
              ></el-input>
            </div>
            <div class="form_item">
              <el-button @click="submitEditForm" type="primary" size="mini"
                >提交</el-button
              >
              <el-button
                @click="editDialogVisible = false"
                type="info"
                size="mini"
                >取消</el-button
              >
            </div>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      curIndex: 0,
      update_area: "",
      data_date: "",
      menulist: ["全部"],
      tableData: [],
      addDialogVisible: false,
      addForm: {
        update_area: "",
        update_person: "",
        update_time: "",
        update_info: ""
      },
      jobs: ["前端", "后端"],
      editDialogVisible: false,
      editForm: {
        update_area: "",
        update_person: "",
        update_time: "",
        update_info: ""
      },
      selectID: [],
      data_id: ""
    };
  },
  methods: {
    selectMenu(idx, val) {
      this.curIndex = idx;
      if (val === "全部") {
        this.data_date = "";
      } else {
        this.data_date = val;
      }

      // 发起请求
      this.initData();
    },
    searchData(val) {
      // 前后端传值
      this.update_area = val;
      this.initData();
    },
    initMenu() {
      let url =
        this._url2 +
        "server/personnel_management/get_down_box_system_update_log/";
      this.$axios.get(url).then(res => {
        if (res.data.code == 200) {
          let concatArr = res.data.data || [];
          this.menulist = this.menulist.concat(concatArr);
        }
      });
    },
    initData() {
      let url =
        this._url2 + "server/personnel_management/select_system_update_log/";
      this.$axios
        .get(url, {
          params: {
            update_area: this.update_area,
            data_date: this.data_date
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data || [];
          }
        });
    },
    cancelAddForm() {
      for (let key in this.addForm) {
        this.addForm[key] = "";
      }
      this.addDialogVisible = false;
    },
    submitAddForm() {
      this.addFormData = new FormData();
      for (let key in this.addForm) {
        if (this.addForm[key] === "" || this.addForm[key] === null) {
          alert("所有字段必填！");
          return;
        }
        this.addFormData.append(key, this.addForm[key]);
      }

      this.addDialogVisible = false;
      // 发送请求
      let url =
        this._url2 + "server/personnel_management/insert_system_update_log/";
      this.$axios.post(url, this.addFormData).then(res => {
        console.log(res);
        if (res.data.code == 200) {
          alert("新增成功！");
          this.initData();
          for (let key in this.addForm) {
            this.addForm[key] = "";
          }
        }
      });
    },
    addNew() {
      this.addDialogVisible = true;
    },
    editData() {
      this.selectID = [];
      let that = this;
      $(".tablebox input:checked").each(function() {
        console.log($(this));
        console.log("选中了", $(this).val());
        that.selectID.push($(this).val());
      });
      // 有延时？使用$nextTick
      if (!$(".tablebox input:checked").length) {
        alert("请选择一个进行编辑！");
        return;
      }
      if ($(".tablebox input:checked").length > 1) {
        alert("请选择一个进行编辑！");
        return;
      }
      // 赋值并弹框
      console.log(this.selectID);

      let curId = this.selectID[0];

      let editItem = this.tableData.find(i => i.id == curId);
      //   this.editForm.sys_name = editItem["sys_name"];

      this.editForm.update_area = editItem["update_area"];
      this.editForm.update_person = editItem["update_person"];
      this.editForm.update_time = editItem["update_time"];
      this.editForm.update_info = editItem["update_info"];
      this.data_id = curId;
      this.editDialogVisible = true;
    },
    deleteData() {
      //
      this.selectID = [];
      let that = this;
      $(".tablebox input:checked").each(function() {
        that.selectID.push($(this).val());
      });
      // 有延时？使用$nextTick
      if (!$(".tablebox input:checked").length) {
        alert("请至少选择一个进行删除！");
        return;
      }
      console.log(this.selectID);
      //   单个删除还是多个删除
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          // 删除
          // let data_id = JSON.stringify(this.selectID);
          let url =
            this._url2 +
            "server/personnel_management/delete_system_update_log/";
          this.$axios
            .get(url, {
              params: {
                data_id: this.selectID
              }
            })
            .then(res => {
              console.log(res);
              if (res.data.code === 200) {
                this.$message("删除成功！");
                this.initData();
                this.selectID = [];
                $(".tablebox input[type='checkbox']").prop("checked", false);
              }
            });
        })
        .catch(err => {
          console.log(err);
        });
    },
    cancelEditForm() {
      console.log("关闭弹框");
      this.editDialogVisible = false;
    },
    submitEditForm() {
      console.log("提交弹框");
      this.editFormData = new FormData();
      console.log(this.editForm);

      for (let key in this.editForm) {
        console.log(key, !this.editForm[key]);
        if (!this.editForm[key] || !this.editForm[key].trim()) {
          alert("所有字段必填！");
          return;
        }

        this.editFormData.append(key, this.editForm[key]);
      }
      this.editDialogVisible = false;
      this.editFormData.append("data_id", this.data_id);

      let url =
        this._url2 + "server/personnel_management/update_system_update_log/";
      this.$axios.post(url, this.editFormData).then(res => {
        if (res.data.code == 200) {
          alert("修改成功");

          // 请求数据
          this.initData();
          this.data_id = "";
        } else {
          alert("修改未成功！");
        }
      });
    },
    cancelEditForm() {}
  },
  created() {
    console.log("giac");
    this.$axios({
      url: this._url2 + "server/stock_management/is_open/",
      method: "get"
    }).then(res => {
      if (res.data.code == 200) {
        this.initMenu();
        this.initData();
      } else {
        alert("你没有权限进入这个页面");
        this.$router.push("/Login");
      }
    });
  }
};
</script>

<style scoped>
h2 {
  font-size: 20px;
  padding: 10px 0;
}
table {
  border-collapse: collapse;
  margin: 0 auto;
}
td,
th {
  border: 1px solid #e5e5e5;
  font-size: 14px;
}
td > div,
th > div {
  width: 200px;
}
td:last-child > div,
th:last-child > div {
  width: 600px;
  /* text-align: left;
  padding: 0 5px; */
}
#SysUpdateLog .form_item p {
  line-height: 40px;
}
#SysUpdateLog .form_item {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}
#SysUpdateLog .addForm {
  margin-right: 40px;
}
#SysUpdateLog .form_item:last-child {
  margin: 0px 70px;
}
#SysUpdateLog .form_item .el-input,
#SysUpdateLog .form_item .el-date-picker,
#SysUpdateLog .form_item .el-textarea {
  width: 222px;
}
#SysUpdateLog .left_menu {
  width: 200px;
  /* height: 100%; */
  font-size: 14px;
  position: absolute;
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
}
#SysUpdateLog .left_menu li {
  line-height: 30px;
  border-bottom: 1px solid #aaa;
}
#SysUpdateLog .left_menu .active {
  color: #fff;
  background: rgba(188, 188, 150, 0.9);
}
#SysUpdateLog .right_con {
  background: rgba(255, 255, 255, 0.9);
  height: 100%;
  text-align: center;
  padding-bottom: 10px;
  margin-left: 250px;
  margin-right: 50px;
}
#SysUpdateLog .TableCon {
  margin: 0 20px;
}
#SysUpdateLog .topOpt {
  margin-bottom: 20px;
}
</style>



